/* common.css */

/* === Button === */
.common-button {
    padding: 8px 16px;
    border-radius: 4px;
    border: none;
    color: white;
    cursor: pointer;
}

.common-button.primary {
    background-color: #3498db;
}

.common-button.secondary {
    background-color: #95a5a6;
}

.common-button.disabled {
    opacity: 0.5;
    cursor: not-allowed;
}


/* === Input === */
.common-input {
    padding: 8px;
    width: 100%;
    border-radius: 4px;
    border: 1px solid #ccc;
}


/* === Select === */
.common-select {
    padding: 8px;
    border-radius: 4px;
    width: 100%;
}


/* === Modal === */
.common-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.common-modal-content {
    background-color: white;
    padding: 24px;
    border-radius: 8px;
    width: 400px;
    max-width: 90%;
}


/* === Spinner === */
.spinner {
    width: 40px;
    height: 40px;
    border: 4px solid #ccc;
    border-top: 4px solid #3498db;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin: auto;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}


/* === Toast === */
.common-toast {
    position: fixed;
    bottom: 20px;
    right: 20px;
    padding: 12px 20px;
    border-radius: 4px;
    color: white;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.2);
}

.common-toast.success {
    background-color: #2ecc71;
}

.common-toast.error {
    background-color: #e74c3c;
}

.common-toast.info {
    background-color: #3498db;
}


/* === Pagination === */
.common-pagination button {
    margin: 0 4px;
    padding: 6px 10px;
    border-radius: 4px;
    border: 1px solid #ccc;
    cursor: pointer;
    background-color: white;
    color: #333;
}

.common-pagination button.active {
    background-color: #3498db;
    color: white;
}

/* === Card === */
.common-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 16px;
    box-shadow: 0 2px 6px rgba(0,0,0,0.1);
    background-color: white;
    margin-bottom: 16px;
}

.common-card-title {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 12px;
}

.common-card-content {
    font-size: 14px;
    color: #333;
}

.common-card-footer {
    margin-top: 12px;
    text-align: right;
}
